"use strict"
const initSqlJs = require('sql.js');
// or if you are in a browser:
// const initSqlJs = window.initSqlJs;

(async function() {
  const SQL = await initSqlJs({
    // Required to load the wasm binary asynchronously. Of course, you can host it wherever you want
    // You can omit locateFile completely when running in node
    locateFile: file => `https://sql.js.org/dist/${file}`
  });

// Read db
  const readBtn = document.body.appendChild(document.createElement("div"))
  readBtn.innerHTML = "Open"
  readBtn.addEventListener('click', readDb);

  async function readDb() {
    //var taBlob = new Blob(binaryArray, {type: 'text/binary'}) //[textArea.value], {type: 'text/plain'});
    const pickerOptions = {
      suggestedName: `my2.db`,
      types: [
        {
          description: 'Simple Text File',
          accept: {
            'application/octet-stream': ['.db'],
          },
        },
      ],
    };
    const [fileHandle] = await window.showOpenFilePicker(pickerOptions);
    const file = await fileHandle.getFile()
    const data = new Uint8Array(await file.arrayBuffer())

    const db = new SQL.Database(data)

    //const db = new SQL.Database();
    // NOTE: You can also use new SQL.Database(data) where
    // data is an Uint8Array representing an SQLite database file


    // Execute a single SQL string that contains multiple statements
    let sqlstr = `CREATE TABLE IF NOT EXISTS hello(a int, b char);
INSERT INTO hello VALUES (0, 'hello');
INSERT INTO hello VALUES (1, 'world');`
    //db.run(sqlstr); // Run the query without returning anything

    sqlstr = `
SELECT name FROM sqlite_master
WHERE type IN ('table','view') AND name NOT LIKE 'sqlite_%'
UNION ALL
SELECT name FROM sqlite_temp_master
WHERE type IN ('table','view')
ORDER BY 1

    `
    {
      const res = db.prepare(sqlstr).getAsObject({})
      console.log(res)
    }


    // Prepare an sql statement
    const stmt = db.prepare("SELECT * FROM hello WHERE a=:aval AND b=:bval");

    // Bind values to the parameters and fetch the results of the query
    const result = stmt.getAsObject({':aval' : 1, ':bval' : 'world'});
    console.log(result); // Will print {a:1, b:'world'}

    // Bind other values
    stmt.bind([0, 'hello']);
    while (stmt.step()) console.log(stmt.get()); // Will print [0, 'hello']
    // free the memory used by the statement
    stmt.free();
    // You can not use your statement anymore once it has been freed.
    // But not freeing your statements causes memory leaks. You don't want that.

    const res = db.exec("SELECT * FROM hello");
    console.log(res)
    /*
    [
      {columns:['a','b'], values:[[0,'hello'],[1,'world']]}
    ]
    */

    // You can also use JavaScript functions inside your SQL code
    // Create the js function you need
    function add(a, b) {return a+b;}
    // Specifies the SQL function's name, the number of it's arguments, and the js function to use
    db.create_function("add_js", add);
    // Run a query in which the function is used
    db.run("INSERT INTO hello VALUES (add_js(7, 3), add_js('Hello ', 'world'));"); // Inserts 10 and 'Hello world'

    // You can create custom aggregation functions, by passing a name
    // and a set of functions to `db.create_aggregate`:
    //
    // - an `init` function. This function receives no argument and returns
    //   the initial value for the state of the aggregate function.
    // - a `step` function. This function takes two arguments
    //    - the current state of the aggregation
    //    - a new value to aggregate to the state
    //  It should return a new value for the state.
    // - a `finalize` function. This function receives a state object, and
    //   returns the final value of the aggregate. It can be omitted, in which case
    //   the final value of the state will be returned directly by the aggregate function.
    //
    // Here is an example aggregation function, `json_agg`, which will collect all
    // input values and return them as a JSON array:
    db.create_aggregate(
      "json_agg",
      {
        init: () => [],
        step: (state, val) => [...state, val],
        finalize: (state) => JSON.stringify(state),
      }
    );

    db.exec("SELECT json_agg(column1) FROM (VALUES ('hello'), ('world'))");
    // -> The result of the query is the string '["hello","world"]'

    // Export the database to an Uint8Array containing the SQLite database file
    const binaryArray = db.export();
    console.log(binaryArray)

    const saveBtn = document.body.appendChild(document.createElement("div"))
    saveBtn.innerHTML = "Click me"
    saveBtn.addEventListener('click', async function(){
      let textArea = document.body.appendChild(document.createElement("textarea"))
      var taBlob = new Blob([binaryArray], {type: 'application/octet-stream'}) //[textArea.value], {type: 'text/plain'});
      const name = `my.db`
      const pickerOptions = {
        suggestedName: `${name.toLowerCase()}.txt`,
        types: [
          {
            description: 'Simple Text File',
            accept: {
              'application/octet-stream': ['.db'],
            },
          },
        ],
      };
      const fileHandle = await window.showSaveFilePicker(pickerOptions);
      const writableFileStream = await fileHandle.createWritable();
      await writableFileStream.write(binaryArray); //taBlob);
      await writableFileStream.close();
    });

    onDeviceReady()

  }

  function onDeviceReady() {
    // check compatibility
    if (!("BarcodeDetector" in globalThis)) {
    console.log("Barcode Detector is not supported by this browser.");
    } else {
    console.log("Barcode Detector supported!");

      // create new detector
      const barcodeDetector = new BarcodeDetector({
        formats: ["code_39", "codabar", "ean_13"],
      });
    }
    detect()
  }

  async function detect() {
      // (function renderLoop() {
      //    requestAnimationFrame(renderLoop);
      //    render();
      //  })();
      const videoList = document.querySelector(`#videoList`)
        ? document.querySelector(`#videoList`)
        : document.body.appendChild(document.createElement("div"))
      videoList.setAttribute(`id`, `videoList`)
      try {
        const mediaStream = await navigator.mediaDevices.getUserMedia({
            video: {facingMode: 'environment'}
        });
        const video = document.createElement('video');
        video.srcObject = mediaStream;
        video.autoplay = true;
        videoList.before(video);
        console.log("success")

        //const barcodeDetector = new BarcodeDetector();
        //const list = document.getElementById('barcode-list');
        //let itemsFound = [];
      } catch (ex) {
        console.log(ex)
      }
  }

})()
